<template>
<Width100>
  <masonry
    :cols="{default: 4, 1000: 3, 700: 2, 400: 1}"
    :gutter="{default: '15px', 700: '20px'}"
    >
    <div v-for="(item, index) in items" :key="index">
      <img v-lazy="item" style="max-width: 100%" />
    </div>
  </masonry>
</Width100>
</template>

<script>
import Width100 from '../common/layout/Width100';

import hammond from '../../assets/img/hammond.svg';
import dva from '../../assets/img/dva.svg';
import ana from '../../assets/img/ana.png';
import ann from '../../assets/img/ann.svg';
import brigitte from '../../assets/img/brigitte.svg';
import palico from '../../assets/img/palico.svg';
import pharah from '../../assets/img/pharah.svg';
import mercy from '../../assets/img/mercy.svg';
import ashe from '../../assets/img/ashe.svg';
import japanbackground from '../../assets/img/japanbackground.svg';
import japan from '../../assets/img/japan.svg';
import cartoon1 from '../../assets/img/cartoon1.svg';
import cartoon2 from '../../assets/img/cartoon2.svg';
import cartoon3 from '../../assets/img/cartoon3.svg';
import cartoon4 from '../../assets/img/cartoon4.svg';
import japanesegirl from '../../assets/img/japanesegirl.svg';
import ignlogos from '../../assets/img/ignlogos.jpg';
import jenjon1 from '../../assets/img/jenjon1.jpg';
import jenjon2 from '../../assets/img/jenjon2.png';
import catchonk from '../../assets/img/catchonk.svg';
import porg from '../../assets/img/porg.gif';
import turtle from '../../assets/img/turtle.png';
import probe from '../../assets/img/probe.png';
import cat from '../../assets/img/cat.gif';
import catcart from '../../assets/img/catcart.gif';

export default {
    name: 'Art',
    components: {
      Width100,
    },
    mounted () {
      this.$ga.page({
        page: '/art',
        title: 'Art',
        location: window.location.href
      });
    },
    data: function() {
      return {
        items: [
          hammond,
          dva,
          ana,
          ann,
          brigitte,
          palico,
          pharah,
          mercy,
          ashe,
          japanbackground,
          japan,
          cartoon1,
          cartoon2,
          cartoon3,
          cartoon4,
          japanesegirl,
          ignlogos,
          jenjon1,
          jenjon2,
          catchonk,
          porg,
          turtle,
          probe,
          cat,
          catcart,
        ]
      }
    }
};
</script>


<style lang="scss" scoped>
.art_row_container {
  width: 80%;
  margin: 0 auto;
}
</style>



